<template>
	<div class="user">
		<br>
		<el-form ref="form" label-width="120px">
			<el-row :gutter="24">
				<el-col :sm="8">
					<el-input placeholder="请输用户名搜索" v-model="pager.user_name" class="input-with-select">
					    <el-button slot="append" icon="el-icon-search" @click="query"></el-button>
					</el-input>
				</el-col>
				<el-col :span="8"></el-col>
			</el-row>
		</el-form>
		<br>
	    <el-table stripe :data="user" border style="width: 100%" height="500">
			<el-table-column fixed prop="user_name" label="用户名" width="80"> </el-table-column>

			<el-table-column prop="isDelete" label="是否禁用" width="80">
				<template slot-scope="scope">
					<el-tag type="success" size="mini" v-if="scope.row.isDelete == 1">正常</el-tag>
					<el-tag type="danger" size="mini" v-else>已禁用</el-tag>
				</template>

			</el-table-column>


			<el-table-column prop="level" label="级别" width="180">
				<template slot-scope="scope">
					<el-tag size="mini" v-if="scope.row.level == 1">超级管理员</el-tag>
					<el-tag size="mini" type="info" v-else-if="scope.row.level == 2">亚普通管理员</el-tag>
					<el-tag size="mini" type="info" v-else-if="scope.row.level == 3">季普通管理员</el-tag>
				</template>

			</el-table-column>


			<el-table-column prop="video_upload_jurisdiction" label="视频上传权限" width="180">
				<template slot-scope="scope">
					<el-tag size="mini" type="success" v-if="scope.row.video_upload_jurisdiction == 1">有权限</el-tag>
					<el-tag size="mini" type="info" v-else>无权限</el-tag>
				</template>

			</el-table-column>

			<el-table-column label="视频上传权限控制" width="200">
				<template slot-scope="scope">
					<el-button-group v-if="scope.row.video_upload_jurisdiction == 1"><el-button type="danger" icon="el-icon-delete" size="mini" @click="edit_video_upload_jurisdiction(scope.row, 'delete')">去除视频上传权限</el-button></el-button-group>

					<el-button-group v-else-if="scope.row.video_upload_jurisdiction == 0"><el-button type="success" icon="el-icon-mobile-phone" size="mini" @click="edit_video_upload_jurisdiction(scope.row, 'show')">启动视频上传权限</el-button></el-button-group>
				</template>
			</el-table-column>


			<el-table-column prop="author" label="创建作者" width="80">

			</el-table-column>
			
			<el-table-column prop="time" label="用户创建时间" width="180">

			</el-table-column>

			<el-table-column prop="ip" label="创建作者地ip" width="110">

			</el-table-column>

			<el-table-column prop="ua" label="创建作者地ua" width="200">

			</el-table-column>



			<el-table-column prop="Jurisdiction" label="权限" width="80">
				<template slot-scope="scope">
					<el-button type="text" disabled v-if="!scope.row.user_menu_jurisdiction && JSON.stringify(scope.row.user_menu_jurisdiction) != '[]'">无任何权限</el-button>
					<el-button type="text" v-else @click="look_jurisdiction(scope.row)">查看权限</el-button>
				</template>
			</el-table-column>



			<el-table-column label="操作" width="200">

			  <template slot-scope="scope">


		          	<el-button-group>
						<el-button type="primary" icon="el-icon-refresh" size="mini" @click="add_jurisdiction(scope.row)">更新权限</el-button>
						<el-button type="danger" size="mini" icon="el-icon-delete" v-if="scope.row.isDelete == 1" @click="deleteUser(scope.row,'delete')">禁用</el-button>
			    		<el-button type="success" icon="el-icon-printer" size="mini" v-else @click="deleteUser(scope.row,'show')">启用</el-button>
			    		
					</el-button-group>
			    

			  </template>

			</el-table-column>


			<el-table-column label="主题创建数量控制" width="100">
				<template slot-scope="scope">
					<el-button-group><el-button type="success" icon="el-icon-refresh" size="mini" @click="userThemeEdit(scope.row)">修改</el-button></el-button-group>
				</template>
			</el-table-column>


		</el-table>
		<div class="paging-box">
			<el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page.sync="pager.currentPage3"
		      :page-size="pager.pageSize"
		      layout="total, sizes, prev, pager, next, jumper"
		      :total="pager.count">
		    </el-pagination>
	    </div>

	    <el-dialog
		  title="权限"
		  :visible.sync="look_jurisdiction_show"
		  width="30%">
		  <el-tree :data="menudata"></el-tree>
		  <span slot="footer" class="dialog-footer">
		    <el-button type="primary" @click="look_jurisdiction_show = false">确 定</el-button>
		  </span>
		</el-dialog>

		<el-dialog
		  title="更新权限"
		  :visible.sync="add_jurisdiction_show"
		  width="30%">
		  <el-tree :data="addmenudata" show-checkbox
  default-expand-all
  node-key="id"
  ref="setmenu"
  highlight-current @check-change="getJurisdiction"></el-tree>
		  <span slot="footer" class="dialog-footer">
		    <el-button type="primary" @click="uploadUserMenu">确 定</el-button>
		  </span>
		</el-dialog>


		<el-dialog
		  title="主题数量修改"
		  :visible.sync="themeNumberShow"
		  width="30%">
		  <el-input-number size="mini" v-model="theme_number"></el-input-number>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="themeNumberShow = false">取 消</el-button>
		    <el-button type="primary" @click="themeNumberEdit">确 定</el-button>
		  </span>
		</el-dialog>



	</div>
</template>

<script>
export default {
	data () {
		return {
			user: null,
			look_jurisdiction_show: false,
			add_jurisdiction_show: false,
			themeNumberShow:false,
			menudata: null,
			addmenudata: null,
			uploadUserMenuId:0,
			theme_number: 0,
			theme_id: 0,
			theme_userId: 0,
			user_menu_jurisdiction_set: '',
			user_menu_jurisdiction: '',
			pager: {
				currentPage3: 1,
				count: 0,
				pageSize: 10,
				size: 0,
				user_name: ''
			},
			menuTreeData:{
	        	name:'menu_name',
	        	href:'menu_href',
	        	icon:'menu_icon',
	        	parentId:'cid',
	        	hrefP: 'menu_href'
	        }
		}
	},
	created () {
		this.getUserData()
		this.getMenuData()
	},
	methods: {
		edit_video_upload_jurisdiction (item,type) {
			var _this = this
		    this.$axios({
		         type: '$axios',
		         obj: {
		          url: _this.api.deletevideo_upload_jurisdiction,
		          method: 'post',
		          success (res) {
		            if (res.status == 200) {
		              _this.alertsu('视频编辑权限禁用成功！')
		              _this.getUserData()
		            } else {
		              _this.alertsu('视频编辑权限禁用失败！')
		            }
		          },
		          error () {
		            _this.alertsu('视频编辑权限禁用失败！')
		          },
		          data: {
		            id: item.id,
		            type: type
		          },
		          header: {
		            'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
		            'content-type': 'application/json; charset=utf-8'
		          }
		        }
		    })
		},
		userThemeEdit (item) {
			this.themeNumberShow = true
			this.theme_number = item.theme_number
			this.theme_id = item.id
		},
		getUserData () {
			var _this = this
			_this.$get({
				type: '$get',
				obj: {
				  url: _this.api.getuser,
				  success: function (res) {
				    if (res.status == 200) {
				      _this.user = res.data.user
				      _this.pager.count = res.data.count
				    }
				    else {
				      _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
				    }

				  },
				  error: function () {
				    _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
				  }
				},
				param: {
				  size: _this.pager.size,
				  sizes: _this.pager.pageSize,
				  search: _this.pager.user_name
				}
			})
		},
		themeNumberEdit () {
			var _this = this
			this.$axios({
				 type: '$axios',
				 obj: {
				  url: _this.api.modifyhhemenumber,
				  method: 'post',
				  success (res) {
				  	_this.themeNumberShow = false
				    if (res.data.code == 200) {
				      _this.getUserData()
				      _this.alertsu('编用户主题数量修改成功！')
				    } else {
				      _this.alerter('编用户主题数量修改失败！')
				    }
				  },
				  error () {
				    _this.alerter('编用户主题数量修改失败！')
				  },
				  data: {
				    theme_number: _this.theme_number,
				    id: _this.theme_id
				  },
				  header: {
				    'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
				    'content-type': 'application/json; charset=utf-8'
				  }
				}
			})
		},
		handleClick () {
		},
		isDeleteFn (item) {
			if (item.isDelete == 1) {
				return '正常'
			} else {
				return '已禁用'
			}
		},
		// pageSize 改变时会触发	每页条数
		handleSizeChange (val) {
			this.pager.pageSize = val
			this.getUserData()
		},
		handleCurrentChange (val) {
			this.pager.size = val - 1
			this.getUserData()
		},
		query () {
			this.pager.size = 0
			this.pager.pageSize = 10
			this.pager.count = 0
			this.getUserData()
		},
		// 查看权限
		look_jurisdiction (item) {
			this.menudata = JSON.parse(item.user_menu_jurisdiction)
			this.look_jurisdiction_show = true
		},
		add_jurisdiction (item) {
			var _this = this
			_this.uploadUserMenuId = item.id
			_this.add_jurisdiction_show = true
			_this.user_menu_jurisdiction_set = item.user_menu_jurisdiction_set
			_this.user_menu_jurisdiction = item.user_menu_jurisdiction
			setTimeout(() => {
				var setmenu = JSON.parse(item.user_menu_jurisdiction_set)
				_this.$refs.setmenu.setCheckedNodes(setmenu);
			},200)
			
		},
		getMenuData () {
	      var _this = this
	      _this.$get({
	        type: '$get',
	        obj: {
	          url: _this.api.getmenu,
	          success: function (res) {
	            if (res.status == 200) {
	              _this.alertsu('获取菜单数据成功！','获取菜单数据',2000)
	              var treedata = _this.tree1(res.data, _this.menuTreeData)
	              var treedata2 = _this.data2treeDG(treedata[0], treedata[1], _this.menuTreeData)
	              _this.addmenudata = treedata2
	            }
	            else {
	              _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
	            }
	          },
	          error: function () {
	            _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
	          }
	        },
	        param: {
	          id: 1
	        }
	      })
	    },
	    uploadUserMenu () {
	      var _this = this
	      this.$axios({
	         type: '$axios',
	         obj: {
	          url: _this.api.uploadUserMenu,
	          method: 'post',
	          success (res) {
	          	_this.add_jurisdiction_show = false
	            if (res.status == 200) {
	              _this.getUserData()
	              _this.alertsu('编辑权限成功！')
	              _this.alert({
	                content: '编辑权限成功！',
	                title: '提示'
	              })
	              _this.getMenuData()
	            } else {
	              _this.alertsu('编辑权限失败！')
	            }
	          },
	          error () {
	            _this.alertsu('编辑权限失败！')
	          },
	          data: {
	            id: _this.uploadUserMenuId,
	            user_menu_jurisdiction: _this.user_menu_jurisdiction,
	            user_menu_jurisdiction_set: _this.user_menu_jurisdiction_set
	          },
	          header: {
	            'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
	            'content-type': 'application/json; charset=utf-8'
	          }
	        }
	      })
	    },
	    getJurisdiction () {
	    	var _this = this
	    	var menudata = this.$refs.setmenu.getAllCheckedNodes()
	    	var setmenudata = this.$refs.setmenu.getCheckedNodes()
	    	menudata.forEach(function(item){
	    		delete item.children
	    	})
	    	var treedata = _this.tree1(menudata)
	    	var treedata2 = _this.data2treeDG(treedata[0], treedata[1])
	    	_this.user_menu_jurisdiction = JSON.stringify(treedata2)
	    	_this.user_menu_jurisdiction_set = JSON.stringify(setmenudata)
	    },
	    deleteUser (item,type) {
	    	var _this = this
		    this.$axios({
		         type: '$axios',
		         obj: {
		          url: _this.api.deleteuser,
		          method: 'post',
		          success (res) {
		            if (res.status == 200) {
		              _this.alertsu('用户禁用成功！')
		              _this.getUserData()
		            } else {
		              _this.alertsu('用户禁用失败！')
		            }
		          },
		          error () {
		            _this.alertsu('用户禁用失败！')
		          },
		          data: {
		            id: item.id,
		            type: type
		          },
		          header: {
		            'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
		            'content-type': 'application/json; charset=utf-8'
		          }
		        }
		    })
	    }
	}
}
</script>
<style>
.user .el-input__icon{
	line-height: inherit;
}
.paging-box{
	padding-top:20px;text-align:right;
}
</style>


